<template>
  <div>
    <h3 class="title is-3">Navigation Using Script</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Script Execution</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can navigate by executing a script.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Execute a script to navigate to a page</li>
            <li>Halts for 10 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavScript",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.execute_script("window.location.href='https://slackingslacker.github.io/seleniumindex#/about';")
time.sleep(10)

driver.close()
      `
    };
  }
};
</script>
