<template>
  <div>
    <h3 class="title is-3">Explicit Wait - URL Matches Regex</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For URL matches a regex</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait if the URL matches a regex.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method 2 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitUrlMatches",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
url = "https://slackingslacker.github.io/seleniumindex#/seleniumwait"
driver.get(url)

def wait_for_url_matches(wait_time: int, matcher_re: str):
    try:
        print("[{}] Matcher text {}".format(str(datetime.now()), matcher_re))
        WebDriverWait(driver, wait_time).until(
            EC.url_matches(matcher_re)
        )
        print("[{}] URL contains".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] URL does not contain".format(str(datetime.now())))

wait_for_url_matches(3, "[^a-zA-Z0-9]")
wait_for_url_matches(6, "[0-9]")
driver.close()
      `,
      output: `[2020-06-17 23:39:26.897441] Matcher regex [^a-zA-Z0-9]
[2020-06-17 23:39:26.913063] URL contains
[2020-06-17 23:39:26.913063] Matcher regex [0-9]
[2020-06-17 23:39:32.978851] URL does not contain 
`
    };
  }
};
</script>
