<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Presence Of Elements</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Presence of Elements in a Page</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait for the presence of elements. It does not need to be visible.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex</li>
              <li>Call the Method 3 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitPresenceAll",
  data() {
    return {
      code: `
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex")

def wait_for_the_elements(wait_time: int, el_name: str):
    try:
        print("[{}] Finding element {}".format(str(datetime.now()), el_name))
        WebDriverWait(driver, wait_time).until(
            EC.presence_of_all_elements_located((By.TAG_NAME, el_name))
        )
        print("[{}] Element found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element did not load".format(str(datetime.now())))

wait_for_the_elements(3, "nav")
wait_for_the_elements(6, "table")
wait_for_the_elements(9, "ul")
driver.close()
      `,
      output: `[2020-06-16 15:15:59.116926] Finding element nav
[2020-06-16 15:15:59.179857] Element found
[2020-06-16 15:15:59.179857] Finding element table
[2020-06-16 15:16:05.242485] Element did not load
[2020-06-16 15:16:05.242485] Finding element ul
[2020-06-16 15:16:14.320383] Element did not load
`
    };
  }
};
</script>
