<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Statelesness of Element</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Statelessness of an Element</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait for state of the element to be inactive.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Finds a clickable element</li>
              <li>Call the Method which prints messages in the console</li>
              <li>Clicks the elemnt that we find earlier then navigates to another page</li>
              <li>Sleeps for 5 seconds</li>
              <li>Calls again the Method which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitStateless",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")

def wait_for_state(wait_time: int, el):
    try:
        print("[{}] Waiting for stateless element".format(str(datetime.now())))
        WebDriverWait(driver, wait_time).until(EC.staleness_of(el))
        print("[{}] Element stateless".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element exists".format(str(datetime.now())))

clickable_link = driver.find_element_by_css_selector("#elementStateless > a")
wait_for_state(3, clickable_link)
clickable_link.click()
time.sleep(5)
wait_for_state(6, clickable_link)
driver.quit()
      `,
      output: `[2020-06-16 16:14:32.676001] Waiting for stateless element
[2020-06-16 16:14:35.738955] Element exists
[2020-06-16 16:14:40.943936] Waiting for stateless element
[2020-06-16 16:14:40.966103] Element stateless
`
    };
  }
};
</script>
