<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Title</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Title</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait for a case sensitive exact title text to load.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method 3 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitTitle",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex")

def wait_for_the_title(wait_time: int, doc_title: str):
    try:
        print("[{}] Waiting for title {}".format(str(datetime.now()), doc_title))
        WebDriverWait(driver, wait_time).until(EC.title_is(doc_title))
        print("[{}] Title found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Error waiting for title".format(str(datetime.now())))

wait_for_the_title(3, "Do It Simpler - VUE - Bulma For Scraping")
wait_for_the_title(6, "Not the title")
wait_for_the_title(9, "do it simpler - vue - bulma for scraping")
driver.close()
      `,
      output: `[2020-06-17 13:29:08.222959] Waiting for title Do It Simpler - VUE - Bulma For Scraping
[2020-06-17 13:29:08.229496] Title found
[2020-06-17 13:29:08.229496] Waiting for title Not the title
[2020-06-17 13:29:14.270883] Error waiting for title
[2020-06-17 13:29:14.270883] Waiting for title do it simpler - vue - bulma for scraping
[2020-06-17 13:29:23.296048] Error waiting for title
`
    };
  }
};
</script>
