<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Frame</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Frame</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >Driver can wait for a frame to load. But since we are using HTML 5 and frames are not supported anymore 
        we will be using <b>iframe</b> instead.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method 2 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>

      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitFrame",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")

def wait_for_frame(wait_time: int, selector: str):
    try:
        print("[{}] Waiting for iframe {}".format(str(datetime.now()), selector))
        WebDriverWait(driver, wait_time).until(
            EC.frame_to_be_available_and_switch_to_it((By.CSS_SELECTOR, selector))
        )
        print("[{}] Frame Loaded".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Frame did not load".format(str(datetime.now())))

wait_for_frame(3, "div#elementFrame iframe")
wait_for_frame(6, "div#elementFrames iframe")
driver.close()
      `,
      output: `[2020-06-16 13:04:28.137831] Waiting for iframe div#elementFrame iframe
[2020-06-16 13:04:28.275966] Frame Loaded
[2020-06-16 13:04:28.275966] Waiting for iframe div#elementFrames iframe
[2020-06-16 13:04:34.464659] Frame did not load`
    };
  }
};
</script>
