<template>
  <div>
    <h3 class="title is-3">Navigation driver.get</h3>
    <div class="box">
      <h1 id="const" class="title is-3">driver.get</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >driver.get loads the page for the given URL. You can navigate from website to website.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Browser goes to www.google.com</li>
            <li>Halts for 10 seconds</li>
            <li>Browser goes to https://slackingslacker.github.io/seleniumindex</li>
            <li>Halts for 10 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavGet",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://www.google.com")
time.sleep(10)

driver.get("https://slackingslacker.github.io/seleniumindex")
time.sleep(10)

driver.close()
      `
    };
  }
};
</script>
