<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Element Locator Selected</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Element Using Locator to be Selected</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >Driver can wait for an element to be selected. Instead of finding the element first, 
        the locator can be passed instead.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method 2 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitElLocatorSelected",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")

def wait_for_element_selected(wait_time: int, selector: str):
    try:
        print("[{}] Waiting element {}".format(str(datetime.now()), selector))
        WebDriverWait(driver, wait_time).until(
            EC.element_located_to_be_selected((By.CSS_SELECTOR, selector))
        )
        print("[{}] Element selected".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element not selected".format(str(datetime.now())))

wait_for_element_selected(3, "div#elementSelected input[name='forSelectedRadio']")
wait_for_element_selected(6, "div#elementSelected input[name='notSelectedRadio']")
driver.close()
      `,
      output: `[2020-06-15 19:46:27.130187] Waiting element div#elementSelected input[name='forSelectedRadio']
[2020-06-15 19:46:27.192734] Element selected
[2020-06-15 19:46:27.192734] Waiting element div#elementSelected input[name='notSelectedRadio']
[2020-06-15 19:46:33.455987] Element not selected`
    };
  }
};
</script>
