<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Element Selected</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Element to be Selected</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >Driver can wait for an element to be selected.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Finds a selected radio button and puts it in a variable</li>
              <li>Finds an unselected radio button and puts it in a variable.</li>
              <li>Call the Method 2 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>

      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitElSelected",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")

def wait_for_element_selected(wait_time: int, element):
    try:
        print("[{}] Waiting element".format(str(datetime.now())))
        WebDriverWait(driver, wait_time).until(
            EC.element_to_be_selected(element)
        )
        print("[{}] Element selected".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element not selected".format(str(datetime.now())))

check_el = driver.find_element_by_css_selector("div#elementSelected input[name='forSelectedRadio']")
uncheck_el = driver.find_element_by_css_selector("div#elementSelected input[name='notSelectedRadio']")

wait_for_element_selected(3, check_el)
wait_for_element_selected(6, uncheck_el)
driver.close()
      `,
      output: `[2020-06-12 21:29:41.509204] Waiting element
[2020-06-12 21:29:41.527520] Element selected
[2020-06-12 21:29:41.527520] Waiting element
[2020-06-12 21:29:47.616751] Element not selected`
    };
  }
};
</script>
