<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Window Count</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Window Count</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait for new browser window to open and if equivalent to count.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method which prints messages in the console</li>
              <li>Click a link which opens a new window</li>
              <li>Calls the Method again which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitNumWindow",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")

def wait_for_window(wait_time: int, window_nums: int):
    try:
        print("[{}] Finding window".format(str(datetime.now())))
        WebDriverWait(driver, wait_time).until(EC.number_of_windows_to_be(window_nums))
        print("[{}] Window Opened is {}".format(str(datetime.now()), str(window_nums)))
    except TimeoutException as e:
        print("[{}] Window did not opened".format(str(datetime.now())))

wait_for_window(3, 2)
driver.find_element_by_css_selector("#elementWindow > a").click()
wait_for_window(6, 2)
driver.quit()

      `,
      output: `[2020-06-16 14:49:55.442702] Finding window
[2020-06-16 14:49:58.467215] Window did not opened
[2020-06-16 14:49:58.783697] Finding window
[2020-06-16 14:49:58.799342] Window Opened is 2
`
    };
  }
};
</script>
