<template>
  <div>
    <h3 class="title is-3">Navigation Using History</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Browsing History</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can navigate forward or backwards using browsing history.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Browser goes to https://slackingslacker.github.io/seleniumindex</li>
            <li>Halts for 3 seconds</li>
            <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/about</li>
            <li>Halts for 3 seconds</li>
            <li>Browser goes to https://slackingslacker.github.io/seleniumindex using the back of history</li>
            <li>Halts for 10 seconds</li>
            <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/about using the forward of history</li>
            <li>Halts for 10 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavHistory",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex")
time.sleep(3)
driver.get("https://slackingslacker.github.io/seleniumindex#/about")
time.sleep(3)

driver.back()
time.sleep(10)

driver.forward()
time.sleep(10)

driver.close()
      `
    };
  }
};
</script>
