<template>
  <div>
    <h3 class="title is-3">Navigation Using Anchor</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Anchor Click</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can do a click on an anchor tag to navigate to a page.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Browser goes to https://slackingslacker.github.io/seleniumindex</li>
            <li>Halts for 5 seconds</li>
            <li>
              Finds the anchor tag for the
              <b>About</b> in the top menu
            </li>
            <li>Clicks the link</li>
            <li>Halts for 10 seconds</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavAnchor",
  data() {
    return {
      code: `
from selenium import webdriver
import time

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex")
time.sleep(5)

el = driver.find_element_by_css_selector("div[class='navbar-start'] > a:last-of-type")
el.click()
time.sleep(10)

driver.close()
      `
    };
  }
};
</script>
