<template>
  <div>
    <h3 class="title is-3">Explicit Wait - User Customized</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Customized Wait</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >Driver can wait using a customize class. We will do customizaton on the <b>EC.text_to_be_present_in_element</b>
        which only allows case sensitive text. We will use a case insestive text instead. 
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Created the custome class for check the expected text.</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method 3 time which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>

      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitCustom",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.common.exceptions import TimeoutException
from datetime import datetime


class has_text(object):
    """An expectation for checking that an element has a particular case insensitive text.

    locator - used to find the element
    returns the WebElement once it has the particular css class
    """


    def __init__(self, locator, text: str = ""):
        self.locator = locator
        self.text = text


    def __call__(self, driver):
        element = driver.find_element(*self.locator)  # Finding the referenced element
        if self.text.lower() == element.text.lower():
            return element
        else:
            return False


driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")


def wait_for_text(wait_time: int, selector: str, text_value: str):
    try:
        print("[{}] Finding text {}".format(str(datetime.now()), selector))
        WebDriverWait(driver, wait_time).until(
            has_text((By.CSS_SELECTOR, selector), text_value)
        )
        print("[{}] Text found".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Text did not load".format(str(datetime.now())))


wait_for_text(3, "div#textExpectation > div:nth-of-type(1)", "Expected Text")
wait_for_text(6, "div#textExpectation > div:nth-of-type(2)", "Expected Text")
wait_for_text(9, "div#textExpectation > div:nth-of-type(1)", "expected text")
driver.close()
      `,
      output: `[2020-06-17 23:53:41.138929] Finding text div#textExpectation > div:nth-of-type(1)
[2020-06-17 23:53:41.254901] Text found
[2020-06-17 23:53:41.254901] Finding text div#textExpectation > div:nth-of-type(2)
[2020-06-17 23:53:47.717515] Text did not load
[2020-06-17 23:53:47.717515] Finding text div#textExpectation > div:nth-of-type(1)
[2020-06-17 23:53:47.733020] Text found`
    };
  }
};
</script>
