<template>
  <div>
    <h3 class="title is-3">Explicit Wait - URL Change</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For URL have changed.</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait if the URL changed.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method which prints messages in the console</li>
              <li>Finds a link</li>
              <li>Click the link found</li>
              <li>Call the Method again which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitUrlChange",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
url = "https://slackingslacker.github.io/seleniumindex#/seleniumwait"
driver.get(url)

def wait_for_url_change(wait_time: int, expected_url: str):
    try:
        print("[{}] Current URL {}".format(str(datetime.now()), driver.current_url))
        WebDriverWait(driver, wait_time).until(
            EC.url_changes(expected_url)
        )
        print("[{}] URL changed".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] URL did not changed".format(str(datetime.now())))

wait_for_url_change(3, url)
clickable_link = driver.find_element_by_css_selector("#elementStateless > a")
clickable_link.click()
wait_for_url_change(6, url)
driver.close()
      `,
      output: `[2020-06-17 23:23:42.263868] Current URL https://slackingslacker.github.io/seleniumindex#/seleniumwait
[2020-06-17 23:23:45.327181] URL did not changed
[2020-06-17 23:23:45.477464] Current URL https://slackingslacker.github.io/simple.html
[2020-06-17 23:23:45.546460] URL changed
`
    };
  }
};
</script>
