<template>
  <div>
    <h3 class="title is-3">Explicit Wait - URL Contains Text</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For URL contains text</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait if the URL contains case sensitive text.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method 2 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitUrlContains",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")

def wait_for_url_contains(wait_time: int, text: str):
    try:
        print("[{}] URL contains {}".format(str(datetime.now()), text))
        WebDriverWait(driver, wait_time).until(
            EC.url_contains(text)
        )
        print("[{}] URL has text".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] URL does not contain text ".format(str(datetime.now())))

wait_for_url_contains(3, "seleniumindex")
wait_for_url_contains(6, "simple")
driver.close()

      `,
      output: `[2020-06-17 23:32:25.271453] URL contains seleniumindex
[2020-06-17 23:32:25.356937] URL has text
[2020-06-17 23:32:25.356937] URL contains simple
[2020-06-17 23:32:31.737639] URL does not contain text 
`
    };
  }
};
</script>
