<template>
  <div>
    <h3 class="title is-3">Explicit Wait - Element Invisibility Using Locator</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Element Invisibility Using Locator</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">Driver can wait for the element to be invisible with the use of locator.
          Instead of finding first the element, you can use the selector to wait for the elements invisibility.
        </div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
              <li>Open the firefox browser</li>
              <li>Browser goes to https://slackingslacker.github.io/seleniumindex#/seleniumwait</li>
              <li>Call the Method 4 times which prints messages in the console</li>
              <li>Closes the browser</li>
          </ul>
          <br/>
          Note: Only 1 element is visible and that is the 3rd div.
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>
      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitInvisible",
  data() {
    return {
      code: `
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC
from selenium.common.exceptions import TimeoutException
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.get("https://slackingslacker.github.io/seleniumindex#/seleniumwait")

def wait_for_the_elements(wait_time: int, selector: str):
    try:
        print("[{}] Finding element {}".format(str(datetime.now()), selector))
        WebDriverWait(driver, wait_time).until(
            EC.invisibility_of_element_located((By.CSS_SELECTOR, selector))
        )
        print("[{}] Element is invisible".format(str(datetime.now())))
    except TimeoutException as e:
        print("[{}] Element is visible".format(str(datetime.now())))

wait_for_the_elements(3, "div#hiddenElements > div:nth-of-type(3)")
wait_for_the_elements(6, "div#hiddenElements > div:nth-of-type(1)")
wait_for_the_elements(9, "div#hiddenElements > div:nth-of-type(2)")
wait_for_the_elements(12, "div#hiddenElements > div")
driver.close()
      `,
      output: `[2020-06-16 13:44:54.948287] Finding element div#hiddenElements > div:nth-of-type(3)
[2020-06-16 13:44:58.392434] Element is visible
[2020-06-16 13:44:58.392434] Finding element div#hiddenElements > div:nth-of-type(1)
[2020-06-16 13:44:58.423557] Element is invisible
[2020-06-16 13:44:58.423557] Finding element div#hiddenElements > div:nth-of-type(2)
[2020-06-16 13:44:58.454809] Element is invisible
[2020-06-16 13:44:58.454809] Finding element div#hiddenElements > div
[2020-06-16 13:44:58.476950] Element is invisible
`
    };
  }
};
</script>
