<template>
  <div>
    <h3 class="title is-3">Implicit Wait</h3>
    <div class="box">
      <h1 id="const" class="title is-3">Waiting For Element Implicitly</h1>
      <article class="message is-primary">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div
          class="message-body"
        >Driver can wait for an element for a constant amount of time with the use of implicit wait.</div>
      </article>
      <prism language="python">{{ code }}</prism>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">What The Program Does?</h1>
      <article class="message">
        <span class="icon has-text-primary">
          <i class="fas"></i>
        </span>
        <div class="message-body">
          <ul>
            <li>Open the firefox browser</li>
            <li>Sets the implicit wait to 5 seconds</li>
            <li>Browser goes to https://slackingslacker.github.io/seleniuminde</li>
            <li>Call the Method 3 times which prints messages in the console</li>
            <li>Closes the browser</li>
          </ul>
        </div>
      </article>
    </div>
    <div class="box">
      <h1 id="const" class="title is-3">Console Output</h1>

      <prism language="python">{{ output }}</prism>
    </div>
  </div>
</template>

<script>
export default {
  name: "WaitImplicit",
  data() {
    return {
      code: `
from selenium import webdriver
from datetime import datetime

driver = webdriver.Firefox(executable_path="geckodriver.exe")
driver.implicitly_wait(5)
driver.get("https://slackingslacker.github.io/seleniumindex")

def find_the_element(selector: str):
    try:
        print("[{}] Finding element {}".format(str(datetime.now()), selector))
        driver.find_element_by_css_selector(selector)
        print("[{}] Element found".format(str(datetime.now())))
    except Exception as e:
        print("[{}] Error is {}".format(str(datetime.now()), str(e)))

find_the_element("nav[role='navigation']")
find_the_element("#noneExistentId")
find_the_element("#anotherNoneExistentId")
driver.close()
      `,
      output: `[2020-06-14 18:08:29.398623] Finding element nav[role='navigation']
[2020-06-14 18:08:29.447615] Element found
[2020-06-14 18:08:29.447615] Finding element #noneExistentId
[2020-06-14 18:08:34.460760] Error is Message: Unable to locate element: #noneExistentId

[2020-06-14 18:08:34.460760] Finding element #anotherNoneExistentId
[2020-06-14 18:08:39.483083] Error is Message: Unable to locate element: #anotherNoneExistentId`
    };
  }
};
</script>
